.articles-management {
    padding: 1.25rem;
    font-family: system-ui, -apple-system, 'Segoe UI', sans-serif;
    background-color: #f5f7fa;
    min-height: 100vh;
}

/* Header & Search */
.articles-header {
    margin-bottom: 1.5rem;
}

.articles-title {
    font-size: 1.5rem;
    font-weight: 600;
    color: #2c3e50;
    margin-bottom: 1rem;
}

.search-container {
    display: flex;
    gap: 0.75rem;
    margin-bottom: 1.25rem;
    align-items: center;
    flex-wrap: wrap;
}

.search-input {
    flex: 1;
    min-width: min(100%, 25rem);
    padding: 0.625rem 1rem;
    border: 1px solid #dcdfe6;
    border-radius: 0.375rem;
    font-size: 0.875rem;
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
    background-color: white;
}

.search-input:focus {
    outline: none;
    border-color: #409eff;
    box-shadow: 0 0 0 2px rgb(64 158 255 / 0.1);
}

.search-input::placeholder {
    color: #909399;
}

.clear-button {
    padding: 0.5rem 1rem;
    background-color: #f5f7fa;
    border: 1px solid #dcdfe6;
    border-radius: 0.375rem;
    color: #606266;
    cursor: pointer;
    font-size: 0.875rem;
    transition: all 0.2s ease;
}

.clear-button:hover {
    background-color: #ecf5ff;
    border-color: #c6e2ff;
    color: #409eff;
}

/* Articles info */
.articles-found {
    color: #606266;
    font-size: 0.875rem;
    margin-bottom: 1rem;
    padding: 0.5rem 0;
}

/* Divider */
.divider {
    height: 1px;
    background: linear-gradient(90deg, transparent 0%, #e0e6ed 50%, transparent 100%);
    margin: 1.25rem 0;
}

/* Table */
.articles-table-container {
    background: white;
    border-radius: 0.5rem;
    box-shadow: 0 2px 12px 0 rgb(0 0 0 / 0.1);
    overflow: hidden;
}

.articles-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.875rem;
}

.articles-table th {
    background-color: #f8f9fa;
    padding: 1rem 0.75rem;
    text-align: left;
    font-weight: 600;
    color: #2c3e50;
    border-bottom: 1px solid #e0e6ed;
    position: relative;
}

.articles-table th::after {
    content: '';
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 1px;
    height: 1rem;
    background-color: #e0e6ed;
}

.articles-table th:last-child::after {
    display: none;
}

.articles-table td {
    padding: 1rem 0.75rem;
    border-bottom: 1px solid #f0f2f5;
    color: #606266;
    transition: background-color 0.2s ease;
}

.articles-table tr:last-child td {
    border-bottom: none;
}

.articles-table tr:hover td {
    background-color: #f8fafc;
}

/* Column specific styles */
.article-name {
    font-weight: 500;
    color: #2c3e50;
}

.article-thickness {
    color: #409eff;
    font-weight: 500;
}

.article-material {
    color: #67c23a;
}

/* Files */
.file-link {
    color: #909399;
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 0.25rem;
    transition: color 0.2s ease;
}

.file-link:hover {
    color: #409eff;
}

.file-icon {
    font-size: 1rem;
}

.no-file {
    color: #c0c4cc;
    font-style: italic;
}

/* Action buttons */
.actions-container {
    display: flex;
    gap: 0.5rem;
}

.action-button {
    padding: 0.375rem 0.75rem;
    border: none;
    border-radius: 0.25rem;
    cursor: pointer;
    font-size: 0.8125rem;
    transition: all 0.2s ease;
}

.edit-button {
    background-color: #ecf5ff;
    color: #409eff;
}

.edit-button:hover {
    background-color: #409eff;
    color: white;
}

.delete-button {
    background-color: #fef0f0;
    color: #f56c6c;
}

.delete-button:hover {
    background-color: #f56c6c;
    color: white;
}

/* Tabs */
.tabs-container {
    display: flex;
    border-bottom: 1px solid #e0e6ed;
    margin-bottom: 1.25rem;
    background: white;
    border-radius: 0.5rem 0.5rem 0 0;
    overflow: hidden;
    flex-wrap: wrap;
}

.tab {
    padding: 0.75rem 1.5rem;
    cursor: pointer;
    font-size: 0.875rem;
    font-weight: 500;
    color: #606266;
    transition: all 0.2s ease;
    border-bottom: 2px solid transparent;
    position: relative;
}

.tab.active {
    color: #409eff;
    border-bottom-color: #409eff;
    background-color: #ecf5ff;
}

.tab:hover:not(.active) {
    color: #409eff;
    background-color: #f5f7fa;
}

/* Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(0.625rem);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.articles-table tr {
    animation: fadeIn 0.3s ease-out;
}

/* Responsive */
@media (max-width: 48rem) {
    .articles-management {
        padding: 0.75rem;
    }

    .search-container {
        flex-direction: column;
        align-items: stretch;
    }

    .search-input {
        min-width: 100%;
    }

    .articles-table-container {
        overflow-x: auto;
    }

    .articles-table {
        min-width: 37.5rem;
    }

    .tabs-container {
        overflow-x: auto;
        flex-wrap: nowrap;
    }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
    .search-input,
    .clear-button,
    .articles-table td,
    .file-link,
    .action-button,
    .tab {
        transition: none;
    }

    .articles-table tr {
        animation: none;
    }
}