/* =================================
   СТИЛІ ДЛЯ COMPONENT CuttingJobExecution
   Оптимізована версія
   ================================= */

/* Змінні для кольорів та розмірів */
:root {
    --primary-color: #007bff;
    --primary-dark: #0056b3;
    --success-color: #28a745;
    --success-dark: #1e7e34;
    --danger-color: #dc3545;
    --danger-dark: #c82333;
    --warning-color: #ffc107;
    --warning-bg: #fff3cd;
    --warning-text: #856404;
    --error-bg: #f8d7da;
    --error-text: #721c24;
    --error-border: #f5c6cb;
    --text-dark: #343a40;
    --text-muted: #6c757d;
    --border-color: #dee2e6;
    --light-bg: #f8f9fa;
    --white: #ffffff;
    --shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
    --radius: 8px;
    --transition: all 0.2s ease;
}

/* =================================
   1. БАЗОВІ СТИЛІ
   ================================= */
.execution-container {
    max-width: 75rem;
    margin: 0 auto;
    padding: 1.25rem;
    font-family: 'Inter', system-ui, sans-serif;
    color: var(--text-dark);
    background-color: var(--light-bg);
    border-radius: 0.75rem;
}

.execution-container h1 {
    font-size: 1.8rem;
    color: #004d99;
    border-bottom: 3px solid var(--border-color);
    padding-bottom: 0.625rem;
    margin-bottom: 1.5rem;
}

/* Стани завантаження та помилок */
.loading, .error {
    text-align: center;
    padding: 1.25rem;
    font-size: 1.1rem;
    font-weight: 600;
}

.error-box {
    background-color: var(--error-bg);
    color: var(--error-text);
    border: 1px solid var(--error-border);
    padding: 0.9375rem;
    border-radius: var(--radius);
    margin-bottom: 1.25rem;
    font-weight: 500;
}

/* =================================
   2. КОНТРОЛЬНІ БЛОКИ
   ================================= */
.control-block {
    background-color: var(--white);
    padding: 1.25rem;
    border-radius: var(--radius);
    box-shadow: var(--shadow);
    margin-bottom: 1.25rem;
    display: flex;
    flex-wrap: wrap;
    gap: 1.25rem;
    align-items: flex-end;
}

.control-label {
    display: flex;
    flex-direction: column;
    font-weight: 600;
    font-size: 0.95rem;
    color: #495057;
    flex: 1;
    min-width: min(100%, 12.5rem);
}

.program-input-box {
    width: 100%;
}

.program-input-row {
    display: flex;
    gap: 0.9375rem;
    align-items: flex-end;
    flex-wrap: wrap;
}

.program-input-row .control-label {
    min-width: min(100%, 15.625rem);
}

.program-id-display {
    font-size: 0.85rem;
    color: var(--text-muted);
    margin-top: 0.3125rem;
}

.editing-hint {
    color: var(--warning-color);
    font-weight: 600;
}

/* Поля введення */
.control-input {
    padding: 0.625rem;
    border: 1px solid #ced4da;
    border-radius: 0.375rem;
    margin-top: 0.3125rem;
    font-size: 1rem;
    background-color: var(--white);
    transition: var(--transition);
    width: 100%;
}

.control-input:focus {
    border-color: var(--primary-color);
    box-shadow: 0 0 0 2px rgb(0 123 255 / 0.25);
    outline: none;
}

/* =================================
   3. КНОПКИ
   ================================= */
.control-button {
    padding: 0.625rem 0.9375rem;
    border: none;
    border-radius: 0.375rem;
    font-weight: 700;
    cursor: pointer;
    transition: var(--transition);
    white-space: nowrap;
    align-self: flex-end;
    font-size: 0.875rem;
}

/* Типи кнопок */
.btn-primary { background-color: var(--primary-color); color: var(--white); }
.btn-primary:hover:not(:disabled) { background-color: var(--primary-dark); }

.btn-edit { background-color: #6c757d; color: var(--white); }
.btn-edit:hover:not(:disabled) { background-color: #5a6268; }

.btn-save { background-color: #17a2b8; color: var(--white); }
.btn-save:hover:not(:disabled) { background-color: #138496; }

.btn-success { background-color: var(--success-color); color: var(--white); }
.btn-success:hover:not(:disabled) { background-color: var(--success-dark); }

.btn-danger { background-color: var(--danger-color); color: var(--white); }
.btn-danger:hover:not(:disabled) { background-color: var(--danger-dark); }

/* Стан disabled */
.control-button:disabled,
.control-input:disabled {
    cursor: not-allowed;
    opacity: 0.6;
}

/* =================================
   4. СПОВІЩЕННЯ ТА ПІДКАЗКИ
   ================================= */
.warning-box {
    border: 1px solid var(--warning-color);
    background-color: var(--warning-bg);
    color: var(--warning-text);
    padding: 0.9375rem;
    border-radius: var(--radius);
}

.warning-box p {
    margin: 0 0 0.625rem 0;
}

.warning-box p:last-child {
    margin-bottom: 0;
}

.hint-info {
    margin-top: 0.625rem;
    font-size: 0.9rem;
    color: var(--text-muted);
}

.error-text {
    color: var(--danger-color);
    font-weight: 600;
    text-align: center;
}

/* =================================
   5. ТАБЛИЦЯ РЕЗУЛЬТАТІВ
   ================================= */
.job-results-section {
    background-color: var(--white);
    padding: 1.25rem;
    border-radius: var(--radius);
    box-shadow: var(--shadow);
    margin-top: 1.25rem;
    overflow-x: auto;
}

.full-width-table {
    width: 100%;
    min-width: 43.75rem;
    border-collapse: collapse;
}

.full-width-table th,
.full-width-table td {
    padding: 0.75rem 0.625rem;
    text-align: left;
    border-bottom: 1px solid #e9ecef;
}

.full-width-table th {
    background-color: #e9ecef;
    font-weight: 700;
    color: #495057;
    text-transform: uppercase;
    font-size: 0.85rem;
}

.log-active-row {
    background-color: #f1f8ff;
}

/* Поля введення в таблиці */
.table-input {
    width: 5.625rem;
    padding: 0.375rem;
    border: 1px solid #ced4da;
    border-radius: 0.25rem;
    text-align: center;
    font-size: 1rem;
    transition: background-color 0.1s;
}

.table-input:disabled {
    background-color: #f1f3f5;
    color: var(--text-muted);
}

/* Колонки */
.column-input-narrow { width: 6.25rem; }
.column-narrow { width: 5rem; }

/* =================================
   6. ДОДАТКОВІ ПАРАМЕТРИ
   ================================= */
.additional-log-params {
    display: flex;
    flex-wrap: wrap;
    gap: 0.9375rem;
    padding: 0.9375rem 0;
    border-top: 1px solid var(--border-color);
    margin-top: 1.25rem;
    margin-bottom: 0.625rem;
}

.additional-log-params .control-label {
    min-width: min(100%, 9.375rem);
}

.additional-log-params .control-input {
    max-width: 9.375rem;
}

/* =================================
   7. КНОПКИ ДІЙ
   ================================= */
.action-buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.25rem;
    padding-top: 1.25rem;
    border-top: 1px dashed #ced4da;
}

.action-button {
    width: 100%;
    max-width: 21.875rem;
    padding: 0.9375rem 1.25rem;
    font-size: 1.1rem;
    border-radius: var(--radius);
    box-shadow: 0 4px 6px rgb(0 0 0 / 0.1);
    margin-bottom: 0.5rem;
}

.action-button:last-child {
    margin-bottom: 0;
}

/* =================================
   8. АДАПТИВНІСТЬ
   ================================= */
@media (max-width: 48rem) {
    .execution-container {
        padding: 0.625rem;
    }

    .program-input-row,
    .operator-control,
    .additional-log-params {
        flex-direction: column;
        gap: 0.9375rem;
    }

    .program-input-row .control-label,
    .operator-control .control-label,
    .additional-log-params .control-label {
        min-width: 100%;
    }

    .program-input-row .control-button {
        width: 100%;
    }

    .job-results-section {
        padding: 0.625rem;
    }

    .full-width-table {
        min-width: 37.5rem;
    }

    .additional-log-params .control-input {
        max-width: 100%;
    }

    .action-button {
        max-width: 100%;
    }
}

/* Спрощений медіа-запит для дуже малих екранів */
@media (max-width: 30rem) {
    .execution-container h1 {
        font-size: 1.5rem;
    }

    .control-block {
        padding: 0.9375rem;
    }

    .full-width-table {
        font-size: 0.875rem;
    }

    .full-width-table th,
    .full-width-table td {
        padding: 0.5rem;
    }
}

/* Покращення для темної теми */
@media (prefers-color-scheme: dark) {
    .execution-container {
        background-color: #2d3748;
        color: #e2e8f0;
    }

    .control-block,
    .job-results-section {
        background-color: #4a5568;
        color: #e2e8f0;
    }

    .full-width-table th {
        background-color: #718096;
        color: #1a202c;
    }
}

/* Анімації для кращого UX */
.control-button,
.control-input {
    transform: translateZ(0);
}

.control-button:active:not(:disabled) {
    transform: translateY(1px);
}

/* Поліпшення доступності */
.control-input:focus-visible,
.control-button:focus-visible {
    outline: 2px solid var(--primary-color);
    outline-offset: 2px;
}

/* Скроллбар для таблиці */
.job-results-section::-webkit-scrollbar {
    height: 8px;
}

.job-results-section::-webkit-scrollbar-track {
    background: #f1f1f1;
    border-radius: 4px;
}

.job-results-section::-webkit-scrollbar-thumb {
    background: #c1c1c1;
    border-radius: 4px;
}

.job-results-section::-webkit-scrollbar-thumb:hover {
    background: #a8a8a8;
}